body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html{
  font-family: 'Cutive Mono', monospace;
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.title-box {
  display: absolute;
  font-size: 100%;
  width: 55vw;
  height: 75vh;
  background-color: white;
  padding: 2.5%;
  left: 0%;
  margin: 3%;
  margin-right: 0%;
}

.title {
  font-size: 300%;
}

.responsive {
  width: 100%;
  height: auto;
}

.App-header {
  background-color: #ACC8E5;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.link-animation {
  position: relative;
  color: white;
  text-decoration: none;
}

.link-animation:hover {
  color: #DEE2E5;
}

.link-animation::after {
  content: "";
  position: absolute;
  width: 0;
  left: 0;
  height: 1px;
  background: #DEE2E5;
  -webkit-transition: all 0.3s ease all 0s;
  transition: all 0.3s ease all 0s;
}

.link-animation:hover::after {
  width: 100%;
  bottom: -10%;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

.home-background {
  background-color: white;
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0%;
  left: 0%;
  overflow: auto;
  display: block;
}

.slides {
  display: none;
}

.pictures {
  height: 90vh;
}

.slideshow {
  margin: 1%;
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url(/static/media/bluebottle.42c637a7.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url(/static/media/bluebottle.42c637a7.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 95vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header-name {
  margin: auto;
  text-align: center;
  position: absolute;
  font-size: 8vw;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.header-subtitle {
  font-size: 1.5vw;
}

.about-background {
  background-color: #FCE3C9;
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0%;
  left: 0%;
  overflow: auto;
  display: block;
}

.profile-box {
  float: right;
  width: 25vw;
  background-color: white;
}

.boxed {
  width: 90vw;
  height: 80vh;
  background-color: white;
  border-radius: .5%;
  border: 2px solid #FCD1A4;
  padding: 2.5%;
  box-shadow: 15px -15px 0px 0px #FCD1A4;
  left: 0%;
  margin: 2.5%;
}

.about-shadow {
  position: relative;
  width: 15vw;
  height: 5px;
  background-color: #FCE3C9;
}

.project-background {
  background-color: #ACC8E5;
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0%;
  left: 0%;
  overflow: auto;
  display: block;
}

.project-box {
  display: absolute;
  border-radius: .5%;
  font-size: 100%;
  width: 90vw;
  height: 85vh;
  background-color: white;
  padding: 2.5%;
  padding-top: 1%;
  left: 0%;
  margin: 2.5%;
  margin-bottom: 0%;
}

.project-pictures {
  float: right;
  width: 25vw;
  height: 20vh;
  background-color: white;
  margin-right: 7.5%;
}

.project-shadow {
  position: relative;
  width: 15vw;
  height: 5px;
  background-color: #ACC8E5;
}

.project-titles {
  font-size: 150%;
}

.project-text {
  background-color: white;
  border-radius: .5%;
  width: 85vw;
  height: 65vh;
  border: 1px solid #84A8CC;
  padding: 2.5%;
  margin-top: 2.5%;
}

.coffee-background {
  background-color: white;
  position: relative;
  width: 90vw;
  height: 100vh;
  margin: 0%;
  margin-left: 2.5%;
  left: 0%;
  overflow: auto;
  display: block;
}

.coffee-shadow {
  position: relative;
  width: 15vw;
  height: 5px;
  background-color: #FCE3C9;
}

.coffee-pictures {
  float: right;
  width: 25vw;
  height: 30vh;
  background-color: white;
  margin-top: 0%;
  margin-right: 5%;
}

.text {
  background-color: white;

}

.coffee-link-animation {
  position: relative;
  color: #FCE3C9;
  text-decoration: none;
}

.coffee-link-animation:hover {
  color: #FCD1A4;
}

.coffee-link-animation::after {
  content: "";
  position: absolute;
  width: 0;
  left: 0;
  height: 1px;
  background: #FCD1A4;
  -webkit-transition: all 0.3s ease all 0s;
  transition: all 0.3s ease all 0s;
}

.coffee-link-animation:hover::after {
  width: 100%;
  bottom: -10%;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

form {
  /* Just to center the form on the page */
  position: relative;
  left: 10px;
  width: 25vw;
  /* To see the outline of the form */
  padding-top: 0;
  padding: 15px;
  border: 1px solid #FCD1A4;
  border-radius: 1%;
}

label {
  /* To make sure that all labels have the same size and are properly aligned */
  display: inline-block;
  width: 90px;
  text-align: right;
}

input, textarea {
  font-family: 'Cutive Mono', monospace;
  width: 25vw;
  box-sizing: content-box;

  /* To harmonize the look & feel of text field border */
  border: 1px solid #999;
}

input:focus, textarea:focus {
  /* To give a little highlight on active elements */
  border-color: #000;
}

textarea {
  /* To properly align multiline text fields with their labels */
  vertical-align: top;

  /* To give enough room to type some text */
  height: 5em;
}

.button {
  /* To position the buttons to the same position of the text fields */
  padding-top: 2.5%; /* same size as the label elements */
}

button {
  font-family: 'Cutive Mono', monospace;
  font-size: 85%;
  margin-left: 0%;
}

